.features {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 6em;
  padding-bottom: 5em;
  background-color: var(--background);
}

.feature-intro {
  margin-bottom: 7em;
}

.feature-intro h2,
.feature-intro p {
  text-align: center;
}

.feature-intro h2 {
  color: var(--dark-violet);
  margin-bottom: 0.8em;
}

.feature-intro p {
  color: var(--gray-violet);
}

.feature-container {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5.5em;
}

@media (min-width: 57em) {
  .feature-intro h2 {
    font-size: 4rem;
  }

  .feature-intro p {
    margin: 0 auto;
    width: 60%;
  }

  .feature-container {
    display: grid;
    gap: 1.5em;
    grid-template-columns: 1fr 1fr 1fr;
    padding-bottom: 2em;
    height: 350px;
  }

  #feature-wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 1232px;
  }
}
